(function() {
  'use strict';

  angular
    .module('lappWeb')
    .controller('ProductDetailController', ProductDetailController);

  ProductDetailController.$inject = ['ProductService', '$rootScope'];

  /** @ngInject */
  function ProductDetailController(ProductService, $rootScope) {
    var vm = this;
    vm.quantity = 1;
    var settings = JSON.parse(localStorage.getItem('settings'));
    vm.policies = _.find(settings, {
      'key': 'Policies'
    }).value;

    vm.spinnerDown = function() {
      if (vm.quantity > 1) {
        vm.quantity--;
      }
    };

    vm.spinnerUp = function() {
      if (vm.quantity < vm.productDetail.quantity) {
        vm.quantity++;
      }
    };

    vm.AddProduct = function(product,quantity) {
      product.quantityToAdd=quantity;
      $rootScope.$emit('productAdded', product);
    };

    ProductService.getDetail(vm.productId).then(function(resp) {
      vm.productDetail = resp;
    });



  }
})();
