(function() {
  'use strict';

  angular
    .module('lappWeb')
    .controller('MainController', MainController);

  MainController.$inject = ['MainService'];

  /** @ngInject */
  function MainController(MainService) {
    var vm = this;

    if (localStorage.getItem("settings") === null) {
      MainService.getSettings();
    }
    angular.element(document.querySelector('.fixed-header-margin')).css({
      'padding-top': angular.element(document.querySelector('header')).outerHeight(true)
    });

    //TODO: Los productos deberia traer la cateroriaID y la subcategoriaID de una vez
    MainService.get().then(function(resp) {
      var categories = _.filter(resp, function(categorie) {
        return categorie.parentId === null;
      });
      vm.categories = _.orderBy(categories, ['name'], ['asc']);

      MainService.products().then(function(products) {
        console.log(products);
        vm.products = products;
      })

    });

  }
})();
